.preloader {
  z-index: 15;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  background-color: #FFF;
  align-items: center;
  justify-content: center;
  @include transition(.3s all .4s);
  img {
    -webkit-animation: 1.2s infinite preloader-heart;
    -moz-animation: 1.2s infinite preloader-heart;
    -o-animation: 1.2s infinite preloader-heart;
    animation: 1.2s infinite preloader-heart;
    max-width: 100%;
    max-height: 100%;
    width: 400px;
    @include transition(.3s all);
    will-change: transform;
    @include transform(scale(1)); }
  &.fast {
    img {
      -webkit-animation-duration: 0.3s;
      -moz-animation-duration: 0.3s;
      -o-animation-duration: 0.3s;
      animation-duration: 0.3s; } }
  &.hide {
    opacity: 0;
    img {
      -webkit-animation: 0.4s linear both preloader-die;
      -moz-animation: 0.4s linear both preloader-die;
      -o-animation: 0.4s linear both preloader-die;
      animation: 0.4s linear both preloader-die;
      @include transform(scale(0)); } } }
.modal-bg, .modal-callback, .modal-success {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw; }
.modal-star {
  z-index: 11;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  min-height: 100vh;
  width: 100vw;
