  &.show {
    -webkit-animation: .3s linear .2s forwards show-callback;
    -moz-animation: .3s linear .2s forwards show-callback;
    -o-animation: .3s linear .2s forwards show-callback;
    animation: .3s linear .2s forwards show-callback;
    transition-delay: .2s;
    @include transform(scale(1)); }
  &.hide {
    -webkit-animation: .3s linear 0s forwards show-callback-rev;
    -moz-animation: .3s linear 0s forwards show-callback-rev;
    -o-animation: .3s linear 0s forwards show-callback-rev;
    animation: .3s linear 0s forwards show-callback-rev; }
  &__image {
    display: block;
    padding-top: 10px;
    width: 100%; }
  &__block {
    position: relative;
    overflow: hidden;
